import React, { useContext } from "react";
import Location from "./Location";
import { LocationContext } from "./_context/CGeolocation";

const Result = () => {
  const {
    weather,
    location,
    ifPickLocation,
    setIfPickLocation,
    ifPickReport,
    GEOGRAPHICAL_LOCATION,
  } = useContext(LocationContext);
  return (
    <React.Fragment>
      <div
        className={`weather-search-result ${weather.length > 0 && location.length > 0 && !ifPickLocation
          ? "weather-search-result-show"
          : ""
          }`}
      >
        <div
          className={`location-result ${location.length > 0 ? "show-location-result" : ""
            }`}
        >
          <ul>
            {weather.map((w) => {
              return (
                <div
                  className="location-data"
                  key={w.cityID}
                  onClick={(e) => {
                    setIfPickLocation(true);
                    GEOGRAPHICAL_LOCATION(w.lat, w.lon, w.lcon, w.ldeg);
                  }}
                >
                  <Location
                    name={w.name}
                    flag={w.flag}
                    temp={w.deg}
                    icon={w.icon}
                    lat={w.lat}
                    lon={w.lon}
                    loadThis={GEOGRAPHICAL_LOCATION}
                  />
                </div>
              );
            })}
          </ul>
        </div>
      </div>
    </React.Fragment>
  );
};

export default Result;
