import React, { useContext } from "react";
import { LocationContext } from "./_context/CGeolocation";
import Search from "./Search";
import Result from "./Result";
import Forecast from "./Forecast";
import Report from "./Report";
import DHM from "./DHM";

const Weather = () => {
  const { location, fLocation, weather, ifPickLocation, ifPickReport, currentLocationReport } = useContext(LocationContext);
  const currrentLocationToggle = weather.length > 0 && (location.length > 0 && !ifPickLocation) || ifPickReport;
  return (
    <React.Fragment>
      <div className="current-location-info">
        <div className={`current-location-data ${currrentLocationToggle
          ? "current-location-data-hide"
          : ""
          }`}>
          <Search />

          <Forecast />
          <Report />
        </div>
        <Result />
        <DHM />
      </div>
    </React.Fragment>
  );
};

export default Weather;
