import React, { useContext } from "react";
import { LocationContext } from "./_context/CGeolocation";
import FeatherIcon from "feather-icons-react";

const DHM = () => {
    const { currentLocation, ifLoaded, ifPickReport, setIfPickReport, currentLocationReport } = useContext(LocationContext);

    return (
        <React.Fragment>
            <div style={{ pointerEvents: !ifLoaded ? "none" : "auto" }} className={`current-location-dhm ${currentLocation && ifLoaded && ifPickReport ? "current-location-dhm-show" : ""}`}>
                <p className="current-location-report-back" onClick={() => setIfPickReport(false)}> <FeatherIcon icon="chevron-left" size="15" /> <span>Back</span></p>
                <div className="current-location-dhm-c">

                    <ul>
                        {currentLocationReport["forecast"] && currentLocationReport["forecast"].map((e, i) => {
                            return (
                                <React.Fragment key={i}>
                                    <div className="location-data">
                                        <li className="location-info">
                                            <span className="location-info-name-report">{e["sky"]}</span>
                                            <span className="location-info-cicon-report">
                                                <img className="location-info-icon-report" src={e["icon"]} />
                                            </span>
                                            <span className="location-info-temp-report">{e["temp"]}</span>

                                            <span className="location-info-coordinates-report">{e["dt"]}</span>
                                        </li>
                                    </div>
                                </React.Fragment>
                            )
                        })
                        }

                    </ul>
                </div>
            </div>
        </React.Fragment >
    )
}

export default DHM
