import React, { createContext, useState, useEffect, useRef, useReducer } from "react";
import axios from "axios";
import publicIp from "public-ip";
import { REDUCER_GEOGRAPHICAL_REPORTS } from "../_reducer/RGeolocation";

export const LocationContext = createContext();

export const LocationsProvider = ({ children }) => {
  const [location, fLocation] = useState("");
  const [currentLocation, setCurrentLocation] = useState();
  const [currentLocationReport, currentLocationReportDispatch] = useReducer(REDUCER_GEOGRAPHICAL_REPORTS, []);
  const [weather, setWeather] = useState([]);
  const [ifLoaded, setIfLoaded] = useState(false);
  const [IfLoadedPercentage, setIfLoadedPercentage] = useState(0);
  const [ifPickLocation, setIfPickLocation] = useState(false);
  const [ifPickReport, setIfPickReport] = useState(false);
  const onLoad = useRef(true);
  const KELVIN_FORMULA = 273.1;
  const WEATHER_LOCATIONS = async (ifRunAgain = false) => {
    console.log(ifRunAgain)
    setIfLoaded(false);
    setIfLoadedPercentage(0);
    try {
      if (location.length > 0) {
        const response = await axios.get(
          process.env.REACT_APP_COUNTRY_LIST + location,
          {
            onDownloadProgress: (progressEvent) => {
              let percentCompleted = Math.round(
                (progressEvent.loaded * 100) / progressEvent.total
              );
              setIfLoadedPercentage(percentCompleted);
            },
          }
        );
        const data = await response.data;
        const list = data.list;
        let weatherArr = [];
        for (let i = 0; i < list.length; i++) {
          let fstr = `${list[i]["sys"]["country"].toLowerCase()}.png`;
          weatherArr.push({
            cityID: list[i]["id"],
            name: `${list[i]["name"]} ${list[i]["sys"]["country"]}`,
            flag: process.env.REACT_APP_COUNTRY_FLAG + fstr,
            lcon: list[i]["weather"][0]["icon"],
            ldeg: list[i]["main"]["temp"],
            icon: `${process.env.REACT_APP_WEATHER_ICON}${list[i]["weather"][0]["icon"]}.png`,
            deg: `${Math.floor(list[i]["main"]["temp"] - KELVIN_FORMULA)}°`,
            degIcon: "°",
            lat: list[i]["coord"]["lat"],
            lon: list[i]["coord"]["lon"],
          });
        }
        setWeather(weatherArr);
        setIfLoaded(true);
      } else {
        setIfLoaded(true);
        setIfLoadedPercentage(100);
      }
    } catch (err) {
      setIfLoaded(true);
      setIfLoadedPercentage(100);
    }
  };

  const GEOGRAPHICAL_LOCATION = async (
    lat = null,
    lon = null,
    lcon = null,
    ldeg = null
  ) => {
    setIfLoaded(false);
    setIfLoadedPercentage(0);
    try {
      const clientIP = await publicIp.v4();
      const georesponse = await axios.get(
        `${process.env.REACT_APP_GET_IP}${clientIP}/json`,
        {
          onDownloadProgress: (progressEvent) => {
            let percentCompleted = Math.round(
              (progressEvent.loaded * 100) / progressEvent.total
            );
            setIfLoadedPercentage(percentCompleted);
          },
        }
      );
      const geodata = await georesponse.data;
      const response = await axios.get(
        `${process.env.REACT_APP_LOCATION_GEO}&lat=${lat ? lat : geodata.latitude
        }&lon=${lon ? lon : geodata.longitude}`,
        {
          onDownloadProgress: (progressEvent) => {
            let percentCompleted = Math.round(
              (progressEvent.loaded * 100) / progressEvent.total
            );
            setIfLoadedPercentage(percentCompleted);
          },
        }
      );
      const data = await response.data;
      let cname = data["sys"]["country"];
      let fstr = `${data["sys"]["country"].toLowerCase()}.png`;
      let WEATHER = {
        skyType: data["weather"][0]["main"],
        cityID: data["id"],
        coord: data["coord"],
        name: data["name"],
        flag: process.env.REACT_APP_COUNTRY_FLAG + fstr,
        date: new Date(data["dt"] * 1000)
          .toString()
          .split("(")[0]
          .split(" ")
          .slice(0, 3)
          .join(" "),
        icon: `${process.env.REACT_APP_WEATHER_ICON +
          (lcon ? lcon : data["weather"][0]["icon"])
          }@2x.png`,
        deg: `${Math.floor(
          (ldeg ? ldeg : data["main"]["temp"]) - KELVIN_FORMULA
        )}`,
        degIcon: "°",
        lat: data["coord"]["lat"],
        lon: data["coord"]["lon"],
        sky: data["weather"][0]["description"],
        feels: `${Math.floor(
          (ldeg ? ldeg : data["main"]["feels_like"]) - KELVIN_FORMULA
        )}`,
        wind: `${data["wind"]["speed"]}m/s`,
        clouds: `${data["clouds"]["all"]}`,
        pressure: ((data["main"]["pressure"] * 100) / 101325).toFixed(3),
      };
      const country = await axios.get(
        `${process.env.REACT_APP_GET_COUNTRY_DETAILS}${cname}`
      );
      const countryData = await country.data;
      WEATHER["country"] = countryData["name"];

      const coordinatesInfo = await axios.get(
        `${process.env.REACT_APP_GET_LOCATION_INFO}latitude=${lat ? lat : geodata.latitude
        }&longitude=${lon ? lon : geodata.longitude}`
      );
      const coordinatesInfoData = await coordinatesInfo.data;

      WEATHER["locality"] = coordinatesInfoData["locality"];

      setCurrentLocation({
        current: WEATHER
      });
      fLocation("");
      setWeather([]);
      setIfLoaded(true);
    } catch (err) {
      setIfLoaded(true);
      setIfLoadedPercentage(100);
    }
  };

  const GEOGRAPHICAL_REPORTS = async (lat, lon, type) => {
    setIfLoaded(false);
    setIfLoadedPercentage(0);
    try {
      const ONECALL = await axios.get(
        `${process.env.REACT_APP_GET_ONECALL}&lat=${lat}&lon=${lon}`
      );
      const REPORT_FORECASTS = await ONECALL.data;
      currentLocationReportDispatch({ type: type, forecast: REPORT_FORECASTS, default: [] })
      setIfLoaded(true);
      setIfPickReport(true);
    } catch (err) {
      setIfLoaded(true);
      setIfLoadedPercentage(100);
    }
  }


  useEffect(() => {
    if (onLoad.current) {
      onLoad.current = false;
      if ("geolocation" in navigator) {
        navigator.geolocation.getCurrentPosition((position) => {
          const latitude = position.coords.latitude;
          const longitude = position.coords.longitude;
          GEOGRAPHICAL_LOCATION(latitude, longitude);
        });
      }
      GEOGRAPHICAL_LOCATION();
      return;
    } else {
      if (location.length > 0) {
        WEATHER_LOCATIONS();
      } else {
        setWeather([]);
      }
    }
  }, [location]);

  return (
    <React.Fragment>
      <LocationContext.Provider
        value={{
          location,
          fLocation,
          weather,
          ifLoaded,
          IfLoadedPercentage,
          setIfLoaded,
          setIfLoadedPercentage,
          currentLocation,
          currentLocationReport,
          ifPickLocation,
          setIfPickLocation,
          ifPickReport,
          setIfPickReport,
          WEATHER_LOCATIONS,
          GEOGRAPHICAL_LOCATION,
          GEOGRAPHICAL_REPORTS
        }}
      >
        {children}
      </LocationContext.Provider>
    </React.Fragment>
  );
};
