import React, { useContext } from "react";
import { LocationContext } from "./_context/CGeolocation";
import FeatherIcon from "feather-icons-react";

const Forecast = () => {
    const { currentLocation } = useContext(LocationContext);
    return (
        <React.Fragment>
            <div className="current-location-info-data">
                <div className="current-location-today">
                    <div>
                        <p>{`${currentLocation["current"].name} ${currentLocation["current"].locality}`}</p>
                        <p className="current-location-yours">
                            <span>
                                {currentLocation["current"].country}
                                <img src={currentLocation["current"].flag} />
                            </span>
                        </p>
                        <p>
                            {currentLocation["current"].date} , Feels like {currentLocation["current"].feels} <span>{currentLocation["current"].degIcon}C</span>
                        </p>
                    </div>
                </div>
                <p className="current-location-deg">
                    {currentLocation["current"].deg}
                    <span>
                        <span>{currentLocation["current"].degIcon}</span>C
                    </span>
                </p>
                <p className="current-location-sky">{currentLocation["current"].sky} <img src={currentLocation["current"].icon} className="current-location-icon" /> </p>
            </div>
            <div className="current-location-temp">
                <div className="current-location-weather">
                    <div className="current-location-wind">
                        <FeatherIcon icon="wind" size="15" />
                        <span>{currentLocation["current"].wind}</span>
                    </div>
                    <div className="current-location-wind">
                        <FeatherIcon icon="cloud" size="15" />
                        <span>{currentLocation["current"].clouds}%</span>
                    </div>
                    <div className="current-location-wind">
                        <FeatherIcon icon="alert-circle" size="15" />
                        <span>
                            {currentLocation["current"].pressure} <span className="mbar">hpa</span>
                        </span>
                    </div>
                </div>
            </div>
        </React.Fragment>
    )
}

export default Forecast
