export const REDUCER_GEOGRAPHICAL_REPORTS = (state, action) => {
    const forecast = [];
    switch (action.type) {
        case "hourly":
            const hourly = action.forecast["hourly"];
            forecast.length = 0;
            for (let i = 0; i < hourly.length; i++) {
                const obj = {};
                const dt = new Date(hourly[i]["dt"] * 1000);
                const tf = dt.getUTCHours() % 12 + 1;
                const d = dt.toLocaleDateString('en-US', { weekday: 'short' });
                const t = `${tf}${dt.getUTCHours() > 12 ? "pm" : "am"}`;
                const mdt = `${d} of ${t}`;
                obj.dt = mdt;
                obj.icon = `${process.env.REACT_APP_WEATHER_ICON}${hourly[i]["weather"][0]["icon"]}@2x.png`;
                obj.sky = hourly[i]["weather"][0]["description"];
                obj.temp = `${hourly[i]["temp"]} °C`;
                forecast.push(obj);
            }
            return { forecast, type: action.type }
        case "daily":
            const daily = action.forecast["daily"];
            forecast.length = 0;
            for (let i = 0; i < daily.length; i++) {
                const obj = {};
                console.log(daily[i])
                const dt = new Date(daily[i]["dt"] * 1000);
                const tf = dt.getUTCHours() % 12 + 1;
                const d = dt.toLocaleDateString('en-US', { weekday: 'long' });
                const mdt = `${d}`;
                obj.dt = mdt;
                obj.icon = `${process.env.REACT_APP_WEATHER_ICON}${daily[i]["weather"][0]["icon"]}@2x.png`;
                obj.sky = daily[i]["weather"][0]["description"];
                obj.temp = `${daily[i]["temp"]["max"]} °C`;
                forecast.push(obj);
            }
            return { forecast, type: action.type }
        default:
            return { forecast: action.default, type: action.type }
    }
}