import React from "react";

const Location = ({ name, flag, temp, icon, lat, lon }) => {
  return (
    <React.Fragment>
      <li className="location-info">
        <span className="location-info-name">{name}</span>
        <span className="location-info-cflag">
          <img className="location-info-flag" src={flag} />
        </span>
        <span className="location-info-temp">{temp}</span>
        <span className="location-info-cicon">
          <img className="location-info-icon" src={icon} />
        </span>
        <span className="location-info-coordinates">{`${lat} ${lon}`}</span>
      </li>
    </React.Fragment>
  );
};

export default Location;
