import React, { useContext } from "react";
import { LocationContext } from "./_context/CGeolocation";
import { DebounceInput } from "react-debounce-input";

const Search = () => {
  const { location, fLocation, ifLoaded, setIfPickLocation, ifPickReport } = useContext(
    LocationContext
  );

  return (
    <React.Fragment>
      <DebounceInput
        type="text"
        className={`weather-search`}
        placeholder="Enter Location"
        disabled={(!ifLoaded && ifPickReport) || (!ifLoaded && !ifPickReport) ? true : false}
        onFocus={() => {
          setIfPickLocation(false);
        }}
        debounceTimeout={700}
        value={location}
        onChange={(e) => {
          fLocation(e.target.value);
        }}
      />


    </React.Fragment>
  );
};

export default Search;
