import React, { useContext } from "react";
import { LocationContext } from "./_context/CGeolocation";
import FeatherIcon from "feather-icons-react";

const Report = () => {
    const { currentLocation, GEOGRAPHICAL_REPORTS } = useContext(LocationContext);
    return (
        <React.Fragment>
            <div className="current-location-reports">
                {/* <div className="current-location-report-type" onClick={() => GEOGRAPHICAL_REPORTS(currentLocation["current"]["coord"]["lat"], currentLocation["current"]["coord"]["lon"], "minutely")}>
                    <p>
                        Minutely Forecast{" "}
                        <span>
                            <FeatherIcon icon="chevron-right" size="15" />
                        </span>
                    </p>
                </div> */}
                <div className="current-location-report-type" onClick={() => GEOGRAPHICAL_REPORTS(currentLocation["current"]["coord"]["lat"], currentLocation["current"]["coord"]["lon"], "hourly")}>
                    <p>
                        3 Days Hourly Forecast{" "}
                        <span>
                            <FeatherIcon icon="chevron-right" size="15" />
                        </span>
                    </p>
                </div>
                <div className="current-location-report-type" onClick={() => GEOGRAPHICAL_REPORTS(currentLocation["current"]["coord"]["lat"], currentLocation["current"]["coord"]["lon"], "daily")}>
                    <p>
                        7 Days Forecast{" "}
                        <span>
                            <FeatherIcon icon="chevron-right" size="15" />
                        </span>
                    </p>
                </div>
            </div>
        </React.Fragment>
    )
}

export default Report
