import React, { useContext } from "react";
import IndeterminateiLoader from "./Loader";
import { LocationContext } from "./_context/CGeolocation";
import Weather from "./Weather";

const Main = () => {
  const { ifLoaded, currentLocation } = useContext(LocationContext);
  return (
    <React.Fragment>
      {!ifLoaded && <IndeterminateiLoader bgcolor="#706dc5" />}
      <div className="weather-main-app">
        <div className="weather-info">
          {currentLocation && (
            <Weather />
          )}
        </div>
      </div>
    </React.Fragment>
  );
};

export default Main;
